@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Varela&display=swap');


*{
  font-family: "poppins";
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body{
  background-color: rgb(255, 255, 255);
}
::-webkit-scrollbar {
      width: 12px;
    }

    ::-webkit-scrollbar-thumb {
      background-color: darkblue; /* Change this to your desired thumb color */
      border-radius: 6px;
    }

    ::-webkit-scrollbar-track {
      background-color: #f1f1f1; /* Change this to your desired track color */
      border-radius: 6px;
    }